:root {
  --highlight-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

* {
  box-sizing: border-box;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav {
  justify-content: space-between;
  height: 60px;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__link {
  display: inline-block;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  white-space: nowrap;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--highlight-color);
  border-radius: 3px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active, :hover {
  color: var(--highlight-color);
}
